html, body {
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden;
  background-color: #0f2537; /* Your background color fills the entire screen */
}

.page-background {
  width: 100vw;
  height: 100vh;
  
  background: linear-gradient(135deg, #3e3d5c 0%, #4d4a6b 50%, #3e3d5c 100%);
  
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;
}

.dashboard-container {
  background-color: #0f2537;
  display: flex;
  width: 1750px; 
  height: 1000px; 
  max-width: calc(100vw - 20px); /* Minimal margins */
  max-height: calc(100vh - 20px);
  overflow: hidden;
  box-sizing: border-box;
  gap: 15px;
  padding: 15px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.left-container {
  flex: 0 0 120px;
  padding: 10px;
  height: 100%;
  box-sizing: border-box;
}

.right-container {
  flex: 1;
  background-color: #e8e4d6;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 20px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  box-sizing: border-box;
}

.home-button {
  margin-top: 50px;
  text-align: left;
  background-color: transparent;
  border: 1px solid transparent;
}

.home-logo {
  border-radius: 50%;
  width: 80%;
}

.home-button:hover {
  background-color: transparent;
  border: 1px solid transparent;
}

.home-button:active {
  background-color: transparent !important;
  border: 1px solid transparent !important;
}

.btn {
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 0.375rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.container-button {
  border-radius: 5px;
  color: #f9f5ec;
  background-color: #e2777d;
}

.container-button:hover {
  background-color: #cd5b45;
}

.download-div {
  text-align: left;
  margin-top: 30px;
  margin-left: 10px;
}

.download-button {
  margin-top: 5px;
}

.data-selection-div {
  text-align: left;
  margin-top: 50px;
  margin-left: 10px;
}

.stats-attributes-button-div {
  margin-top: 5px;
  display: flex;
  gap: 0.5rem;
}

.upload-div {
  text-align: left;
  font-size: 14px;
  margin-top: 50px;
  margin-left: 10px;
}

.sample-data-div {
  text-align: left;
  font-size: 14px;
  margin-left: 10px;
}

.feedback-report-div {
  text-align: left;
  margin-top: 50px;
  margin-left: 10px;
}

.feedback-report-button-div {
  margin-top: 5px;
  display: flex;
  gap: 0.5rem;
}

label {
  color: #e8e4d6;
}

.stats-label {
  color: #0f2537;
  font-size: 17.5px;
}

/* Table scrollbar */
.scrollbar::-webkit-scrollbar {
  width: 8px;
}

.scrollbar::-webkit-scrollbar-track {
  background: #0f2537; /* Match your paper background */
  border-radius: 5px;
}

.scrollbar::-webkit-scrollbar-thumb {
  background: #e2777d; /* Match your button color */
  border-radius: 5px;
  opacity: 0.7;
}

.scrollbar::-webkit-scrollbar-thumb:hover {
  opacity: 1;
}